
import React from 'react';
import ReactDOM from 'react-dom';

class Sticker extends React.Component {
  state = {down: false}

  componentDidMount() {
    this.refs.image.draggable = false;

    window.addEventListener("touchup", () => {
      this.setState({down: false});
    })
    window.addEventListener("mouseup", () => {
      this.setState({down: false});
    })
  }

  pressDown(e) {
    this.setState({down: true});
    this.moved = false;
  }

  move() {
    this.moved = true;
  }

  up(e) {
    this.setState({down: false});
    if (!this.moved)
      this.props.onClick();
  }

  render() {
    return <img ref="image" onTouchStart={this.pressDown.bind(this)} onMouseDown={this.pressDown.bind(this)} onTouchEnd={this.up.bind(this)} onTouchMove={this.move.bind(this)} onMouseUp={this.up.bind(this)} className={"sticker " + (this.state.down ? "down" : "")} src={"./images/stickers/" + this.props.image + ".png"} />
  }
}

